import React from "react";
import "./styles.css";
import SideBar from "./components/SideBar";
import CardList from "./components/CardList";
import { useState, useEffect } from "react";

export default function App() {
  const [beers, setBeers] = useState([]);

  // fecth data from API
  const getBeers = async () => {
    return await fetch(`https://api.punkapi.com/v2/beers`)
      .then((res) => res.json())
      .then((res) => {
        setBeers(res);
      });
  };

  // fech data from API based on the name of the brew
  const filterBeersByName = (searchTerm) => {
    fetch(`https://api.punkapi.com/v2/beers?beer_name=${searchTerm}`)
    .then((res) => res.json())
    .then((res) => {
      setBeers(res);
    });
  }

  // fetch data from API based on the ABV

  const filterByABV = () => {
      fetch(`https://api.punkapi.com/v2/beers?abv_gt=6`)
      .then((res) => res.json())
      .then((res) => {
        setBeers(res);
      })
  }


  // fetch data prom API based on the range classic

  const filterByRange = () => {
    fetch(`https://api.punkapi.com/v2/beers?brewed_before=10-2011`)
    .then((res) => res.json())
    .then((res) => {
      setBeers(res);
    })
  }

  // fetch data prom API based on ABV and Range
  const filterByAbvAndRange = () => {
    fetch(`https://api.punkapi.com/v2/beers?brewed_before=10-2011&abv_gt=6`)
    .then((res) => res.json())
    .then((res) => {
      setBeers(res);
    })
  }

  useEffect(() => {
    getBeers();
  }, []);



  return (
    <div className="App">
      <h1>Friday beer challenge</h1>
      <SideBar filterBeersByName={filterBeersByName} filterByABV={filterByABV} 
      getBeers={getBeers} filterByRange={filterByRange} filterByAbvAndRange={filterByAbvAndRange}/>
      <CardList beers={beers} />
    </div>
  );
}
