import React from "react";

import SearchBar from "../SearchBar";
import Filter from "../Filter";

const SideBar = (props) => {
  const {filterBeersByName, filterByABV, filterByRange, getBeers, filterByAbvAndRange} = props;

  return (
    <div>
      <SearchBar filterBeersByName={filterBeersByName}/>
      <Filter filterByABV={filterByABV} getBeers={getBeers} filterByRange={filterByRange} filterByAbvAndRange={filterByAbvAndRange}/>
    </div>
  );
};

export default SideBar;
